client.on('message', (topic, message) => {
  switch (topic) {
    case 'PLC/OMRON/STATUS':
      return handleGetStatusPLC(message)
    case 'PLC/OMRON/PUB':
      return handleGetPub(message)
  }
  console.log('No handle for topic:', topic)
})
