function handleGetStatusPLC(message) {
  message = message + '';
  var arr = message.split('|')
  console.log(arr)
  if (arr.length === 2) {
    if (arr[1] === "Connected") {

    }
    if (arr[1] === 'Connected') {
      app.database().ref('Table1/MQTT/STATUS_PLC/' + arr[0]).set({
        Status: 'Connected'
      });
    }
    if (arr[1] === 'Disconnected') {
      app.database().ref('Table1/MQTT/STATUS_PLC/' + arr[0]).set({
        Status: 'Disconnected'
      });
    }
  }
}
