var refCOMPC = app.database().ref('Table1/MQTT/SUBSCRIBE_TO_PLC');
refCOMPC.on('value', (snapshot) => {
  console.log(snapshot.val().COMPC)
  if (snapshot.val().COMPC !== '_') {
    try {
      var arr = snapshot.val().COMPC.split('|');
      if (arr[1] === 'BitAdjust') {
        client.publish('OMRON/' + arr[0] + '/SUB', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }
      if (arr[1] === 'WordAdjust') {
        client.publish('OMRON/' + arr[0] + '/SUB', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }
      if (arr[1] === 'DWordAdjust') {
        client.publish('OMRON/' + arr[0] + '/SUB', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }

      if (arr[1] === 'Refresh_BitAdjust') {
        client.publish('OMRON/' + arr[0] + '/SUBREFRESH', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }
      if (arr[1] === 'Refresh_BitDisplay') {
        client.publish('OMRON/' + arr[0] + '/SUBREFRESH', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }
      if (arr[1] === 'Refresh_WordAdjust') {
        client.publish('OMRON/' + arr[0] + '/SUBREFRESH', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }
      if (arr[1] === 'Refresh_WordDisplay') {
        client.publish('OMRON/' + arr[0] + '/SUBREFRESH', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }
      if (arr[1] === 'Refresh_DWordAdjust') {
        client.publish('OMRON/' + arr[0] + '/SUBREFRESH', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }
      if (arr[1] === 'Refresh_DWordDisplay') {
        client.publish('OMRON/' + arr[0] + '/SUBREFRESH', snapshot.val().COMPC + '')
        refCOMPC.child('COMPC').set('_');
      }
    } catch (error) {
      console.log(error)
    }

  }
})
//Time Send Client
setInterval(function () {
  app.database().ref('Table1/MQTT/TIME_CURRENT/').set({
    Time: getDateTime()
  });
}, 5000);
function getDateTime() {

  var date = new Date();

  var hour = date.getHours();
  hour = (hour < 10 ? "0" : "") + hour;

  var min = date.getMinutes();
  min = (min < 10 ? "0" : "") + min;

  var sec = date.getSeconds();
  sec = (sec < 10 ? "0" : "") + sec;

  return hour + ":" + min + ":" + sec;

}
